<template>
  <nav class="nav" :class="{active: isActive}">
    <div class="logo">
      <img src="../assets/logo.png" alt="Vue" />
    </div>

    <span class="material-icons arrow" @click="handleClick">
      keyboard_double_arrow_right
    </span>

    <div class="links">
      <router-link class="link" to="/" @click.native="handleClick">
        <span class="material-icons"> home </span>
        <div class="text">Home</div>
      </router-link>

      <router-link class="link" to="/about" @click.native="handleClick">
        <span class="material-icons"> help </span>
        <div class="text">About</div>
      </router-link>

      <router-link class="link" to="/products" @click.native="handleClick">
        <span class="material-icons"> inventory_2 </span>
        <div class="text">Products</div>
      </router-link>

      <router-link class="link" to="/contact" @click.native="handleClick">
        <span class="material-icons"> mail </span>
        <div class="text">Contact</div>
      </router-link>

      <div class="spacer"></div>

      <router-link class="link" to="/settings" @click.native="handleClick">
        <span class="material-icons"> settings </span>
        <div class="text">Settings</div>
      </router-link>
    </div>
  </nav>
</template>

<script>
export default {
  name: 'TheSidenav',
  data(){
    return{
      isActive: JSON.parse(localStorage.getItem('isActive')) || false,
    }
  },
  methods:{
    handleClick(e){
      if(window.innerWidth <= 900 || e.target.classList.contains('arrow')){
        this.isActive = !this.isActive
        this.setStatus()
      }
    },
    setStatus(){
      localStorage.setItem('isActive', JSON.stringify(this.isActive))
    },
  }
};
</script>

